import React from "react";
import { connect } from "react-redux";
import $ from 'jquery';

import Title from '../components/Title';
import Side from '../components/Side';
import Foot from '../components/Foot';
import { getTargetStudentInfo }  from "../reducers/action";

import './css/follow.css';

class Follow extends React.Component{

	constructor(){
		super();
		this.state = {
			studentName: null,
		}
	}

	// 
	componentWillMount(){
		var isLogin = window.sessionStorage.getItem("isLogin");
		var locationState = this.props.history.location.state;
		if(!isLogin){
			this.props.history.replace("/"); // login
		}else{
			this.setState({
				studentName: locationState.studentName,
			});
		}
	}

	// 
	componentDidMount(){
		this.getTargetStudentInfo();
	}

	//
	getTargetStudentInfo(){

		$.ajax({
			url: "/getStudentInfo",
			type: "post",
			contentType: "application/x-www-form-urlencoded; charser=utf-8",
			data: "student_name=" + this.state.studentName,
			success: function(result){
				this.props.getTargetStudentInfo(result);
			}.bind(this),
			fail: function(err){
				console.log(err);
			}
		});
	}

	backList(){
		this.props.history.goBack();
	}

	render(){
		var item = this.props.targetStudentInfo; // 

		return (
			<div className="page follow">
			    <Title />
				<div className="main clearfix">
					<div className="side f-left">
						<Side /> 
					</div>
					<div className="show f-right">

						<h3 className="show-list-title clearfix">
							<input type="button" value="" 
								onClick={()=>{ this.backList() }} id="backListBtn" />
						</h3>
						<div className="content-list">
							<p><span></span>{item.student_name}</p>
							<p><span></span>{item.student_id}</p>
							<p><span></span>{item.class_name}</p>
							<p><span></span>{item.profession}</p>
							<p><span></span>{item.academe}</p>
							<p><span></span>{item.school}</p>
						</div>
						<h3 className="show-list-title"></h3>
						<div className="content-list">
							<p><span></span>{item.teacher_name}</p>
							<p><span></span>{item.teacher_tel}</p>
							<p><span></span>{item.practice_company}</p>
							<p><span></span>{item.post}</p>
							<p><span></span>{item.practice_time}</p>
							<p><span></span>{item.practice_long}</p>
							<p><span></span>{item.practice_type}</p>

							<p><span></span>{item.relation_name}</p>
							<p><span></span>{item.relation_tel}</p>
							<p><span></span>{item.arrange}</p>
							
							<p><span></span>{item.company_taken}</p>
							<p><span></span>{item.tenBreak}</p>
							<p><span></span>{item.sixteenBreak}</p>
							<p><span>()</span>{item.changed}</p>
							<p><span>()</span>{item.remark}</p>
						</div>

					</div>
				</div>
				<Foot />
			</div>	
		);
	}
}

const mapStateToProps = (state) => {
	return {
		targetStudentInfo: state.targetStudentInfo,
	}
}


const mapDiapatchToPorps = (dispatch) => {
	return{
		getTargetStudentInfo: (val) => { dispatch(getTargetStudentInfo(val)); }
	}
}

export default connect(mapStateToProps, mapDiapatchToPorps)(Follow);


